.complete {
  padding-top: 5rem;
  &__img {
    position: relative;
    border-radius: 5rem;
    overflow: hidden;
    img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
    }
  }

  &__desc {
    &:not(:last-child) {
      margin-bottom: 2.5rem;
    }
  }

  &__content {
    padding: 9rem 7rem;
  }

  &__title {
    line-height: 1.2;
    margin-bottom: 3.5rem;
  }

  &--nes {
    .complete__content {
      padding-top: 9.5rem;
      padding-right: 11rem;
    }
    .complete__desc {
      p {
        &:not(:last-child) {
          margin-bottom: 1rem;
        }
      }
    }
  }

  &--flok {
    padding-bottom: 5rem;
    .complete__content {
      padding-right: 11rem;

      p {
        &:not(:last-child) {
          margin-bottom: 1rem;
        }
      }

      ul,
      ol {
        &:not(:last-child) {
          margin-bottom: 1rem;
        }
        li {
          &:not(:last-child) {
            margin-bottom: 1rem;
          }
        }
      }
    }
  }
}
